:root {
  --primary: #fff;
}

.btn {
  display: inline-block;
  border-radius: 3px;
  background-color: #5fd354;
  border: none;
color: #FFFFFF;
  text-align: center;
  font-size: 25px;
  padding: 10px;
  width: 200px;
  transition: all 0.5s;
  cursor: pointer;
  margin: 5px;
}

.button1 span {
   cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
  
}

.btn--primary {
  background-color: var(--primary);
  color: #242424;
  border: 1px solid var(--primary);
}

.btn--outline {
  background-color: transparent;
  color: #fff;
  padding: 8px 20px;
  border: 1px solid var(--primary);
  transition: all 0.3s ease-out;
}

.btn--medium {
  padding: 8px 20px;
  font-size: 18px;
  font-family:cursive;
}

.btn--large {
 
  padding: 12px 26px;
  font-size: 20px;
  font-family:cursive;

}

.btn--large:hover
 {
  transition: all 0.3s ease-out;
  background: rgb(209, 216, 218);
  color: #242424;
  transition: 250ms;
}
.btn--medium:hover{
  transition: all 0.3s ease-out;
  background:transparent;
  color: #242424;
  transition: 250ms;
}